<template>
  <div class="message-alert">
    <div class="alert alert-dismissible"
      :class="'alert-' + item.status"
      v-for="(item, i) in messages" :key="i">
      {{ item.message }}
      <button type="button" class="close" @click="removeMessage(i)" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Navbar',
    data() {
      return {
        messages: [
          // {
          // message: '訊息內容', //訊息內容
          // status: 'danger', //Bootstrap樣式
          // timestamp: 123 //ID
          // }
        ],
      };
    },
    methods: {
      updateMessage(message, status) {
        const timestamp = Math.floor(new Date() / 1000);
        this.messages.push({
          message,
          status,
          timestamp,
        });
        this.removeMessageWithTiming(timestamp);
      },
      removeMessage(num) { //X按鈕關閉訊息
        this.messages.splice(num, 1);
      },
      removeMessageWithTiming(timestamp) { //每5秒過後自動移除訊息
        const vm = this;
        setTimeout(() => {
          vm.messages.forEach((item, i) => {
            if (item.timestamp === timestamp) {
              vm.messages.splice(i, 1);
            }
          });
        }, 5000);
      },
    },
    created() {
      const vm = this;
       // 自定義名稱 'messsage:push'
      // message: 傳入參數
      // status: 樣式，預設值為 warning
      vm.$bus.$on('messsage:push', (message, status = 'warning') => { //在外層呼叫 Vue 底下的 EventBus，並利用 $on 的放式註冊 messsage:push 的方法
        vm.updateMessage(message, status);
      });
      // vm.$bus.$emit('messsage:push'); //內層觸發（$emit）
    },
  };
</script>

<style lang="scss" scoped>
  .message-alert {
    position: fixed;
    z-index: 1100;
    max-width: 50%;
    top: 10%;
    right: 10%;
  }
</style>